.si-form {
  &__label {
    margin-top: 10px;
    font-weight: bold;
  }

  &__input {
    width: 80%;
    margin-bottom: 10px;
    height: 50px;
    border: none;
    background-color: #f6f6f6;
    padding-left: 24px;
  }
  &__input:focus {
    border: 1px solid grey;
  }

  > * {
    margin-left: 45px;
  }
  &__helpers {
    &__forgot-password {
      padding-left: 35px;
      font-weight: bold;
    }

    .icon-checkbox-unchecked {
      margin-right: 5px;
    }
    .icon-checkbox-checked {
      margin-right: 5px;
      color: var(--color-btn-active);
    }
  }
  &__button-field {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
  }
  &__login-button {
    font-weight: bold;
    color: white;
    margin-top: 50px;
    width: clamp(100px, 375px, 600px);
    height: 50px;
    border: none;
    background-color: var(--color-btn-active);
  }
}
